import React,{useState,useEffect} from 'react';
import '../../css/animate.like.css';
const fecha = ()=>{
  const d = new Date();
  const date = d.toLocaleDateString();
  const hora = d.getHours();
  const minutos = d.getMinutes();
  const time = hora>=12?"PM":"AM";
  const fecha = date+" "+hora+":"+minutos+time;
  return fecha;
}
const visita={
  id:1,
  fecha:fecha(),
  visita:true
}
const user={
  id:1,
}
const HeaderContent = ()=>{
  const [visitas,setVisitas] = useState(0);
useEffect(()=>{

  const handlevisitas = async()=>{
    try{
      fetch("https://portafolioapi.herokuapp.com/visitas",{
        method:"POST",
        headers:{
          "Content-Type":"application/json"
        },
        body:JSON.stringify(visita)
      });
      /*fetch("https://portafolioapi.herokuapp.com/followers",{
        method:"POST",
        headers:{
          "Content-Type":"application/json"
        },
        body:JSON.stringify(user)
      });*/
      const res = await fetch("https://portafolioapi.herokuapp.com/visitas");
      const obj = await res.json();
      let contador = 0;
      obj._embedded.followersVisitasList.forEach((el)=>{
        if(el.visita){
          contador++
        }
      })
      setVisitas(contador)
      
    }catch(e){

    }

  }
  handlevisitas();
},[])
useEffect(()=>{
  let isActive = false;
  const $heart = document.querySelector(".like");
  const $heart2 = document.querySelector(".like2");
  //miStorage.clear()
  const handlecolor =(e)=>{
    
    if(e.target.matches(".like")){
      isActive=!isActive;
      if(isActive){
        e.target.style.setProperty("fill","#FF0000");
        $heart2.style.setProperty("fill","#000000");
      }
      else e.target.style.setProperty("fill","#000000");  
   }
   
    if(e.target.matches(".like2")){
      isActive=!isActive;
      if(isActive){
        e.target.style.setProperty("fill","#FF0000");
        $heart.style.setProperty("fill","#000000");
      }
      else e.target.style.setProperty("fill","#000000");  
   }
   
}
document.addEventListener("click",handlecolor)

},[])


    return(
        <>
        <nav className="level">
          <div className="level-item has-text-centered">
            <div>
              <p className="heading">Visitas</p>
              <p className="title">{visitas}</p>
            </div>
          </div>
          <div className="level-item has-text-centered">
            <div>
              <p className="heading">No me gusta</p>
                <svg className="like" id="like" xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="currentColor" viewBox="0 0 16 16">
                  <path className="like" d="M2.866 14.85c-.078.444.36.791.746.593l4.39-2.256 4.389 2.256c.386.198.824-.149.746-.592l-.83-4.73 3.522-3.356c.33-.314.16-.888-.282-.95l-4.898-.696L8.465.792a.513.513 0 0 0-.927 0L5.354 5.12l-4.898.696c-.441.062-.612.636-.283.95l3.523 3.356-.83 4.73zm4.905-2.767-3.686 1.894.694-3.957a.565.565 0 0 0-.163-.505L1.71 6.745l4.052-.576a.525.525 0 0 0 .393-.288L8 2.223l1.847 3.658a.525.525 0 0 0 .393.288l4.052.575-2.906 2.77a.565.565 0 0 0-.163.506l.694 3.957-3.686-1.894a.503.503 0 0 0-.461 0z"/>
                </svg>
            </div>
          </div>
          <div className="level-item has-text-centered">
            <div>
              <p className="heading">Me gusta</p>
                <svg className="like2" id="like"xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="#fg4516" viewBox="0 0 16 16">
                  <path d="M2.866 14.85c-.078.444.36.791.746.593l4.39-2.256 4.389 2.256c.386.198.824-.149.746-.592l-.83-4.73 3.522-3.356c.33-.314.16-.888-.282-.95l-4.898-.696L8.465.792a.513.513 0 0 0-.927 0L5.354 5.12l-4.898.696c-.441.062-.612.636-.283.95l3.523 3.356-.83 4.73zm4.905-2.767-3.686 1.894.694-3.957a.565.565 0 0 0-.163-.505L1.71 6.745l4.052-.576a.525.525 0 0 0 .393-.288L8 2.223l1.847 3.658a.525.525 0 0 0 .393.288l4.052.575-2.906 2.77a.565.565 0 0 0-.163.506l.694 3.957-3.686-1.894a.503.503 0 0 0-.461 0z"/>
                </svg>
            </div>
          </div>
        </nav>
        <hr/>
        </>
    )
}

export default HeaderContent;